







import Vue, { PropType } from "vue";

export default Vue.extend({
	name: 'editor-message',
	props: {
		message: {
			type: String,
			default: "",
		},
		type: {
			type: String as PropType<"error"|"warning"|"info">,
			default: "info",
		}
	},
	data() {
		return {}
	},
	computed: {
		icon() {
			switch (this.$props.type) {
				case "warning":
					return "!";
				case "error":
					return "x";
				case "info":
				default:
					return "?";
			}
		},
	}
});
